@import "../partials/variables";

//Theme 14
#atw_style14{
    .atw__title14{
        margin-bottom: 50px;
        padding-bottom: 0;
        padding-top: 15px;
        &:before{
            top: 0;
            background: $theme1;
            width: 60px;
        }
    }
    .atw_slider_wrapper{
        position: relative;
        .atw--single_item{
            .atw_item_bottom{
                padding: 20px;
                .atw_item_header{
                    padding: 0;
                    margin-bottom: 10px;
                    .atw_rating{
                        margin-bottom: 10px;
                        display: block;
                    }
                    .atw_item_title{
                        a{
                            font-size: 16px;
                            color: #000;
                        }
                    }
                }
                .atw_item_info{
                    .atw_price{
                        color: $theme1;
                    }
                }
                .atw_product_actions2{
                    justify-content: flex-start;
                    margin-top: 20px;
                    li{
                        margin-right: 0;
                        a{
                            border-radius:0;
                            &:hover{
                                color: #fff;
                                background: $theme1;
                                border-color: $theme1;
                            }
                        }
                        .atw_btn{
                            padding: 0 10px;
                            background: none;
                            color: #9192a3;
                            border: 1px solid $borderColor;
                            line-height: 38px;
                            margin: 0 -1px;
                            &:hover{
                                color: #fff;
                                background: $theme1;
                                border-color: $theme1;
                            }
                        }
                    }
                }
            }
        }
        .atw_slider_controls{
            .slider--control14{
                border: 1px solid $borderColor;
                border-radius: 50%;
                z-index: 2;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                transition: $transition;
                &:hover{
                    border-color: $theme1;
                    color: $theme1;
                }
            }
            .icon-arrow-left{
                left: -20px;
            }
            .icon-arrow-right{
                right: -20px;
            }
        }
    }
}